import React from 'react';
import PropTypes from 'prop-types';
import path from '../../helpers/path';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import APIcon from '../common/APIcon/APIcon';
import APLogo from '../common/APLogo/APLogo';
import UserSummaryContainer from '../UserSummary/UserSummaryContainer';
import PageHeaderNavComponent from './PageHeaderNavComponent';
import { mainNavOptions } from '../../routeTemplates';

import './PageHeaderComponent.css';

const pageHeaderClasses = getBEMClasses('page-header');

const PageHeaderComponent = (props, context) => {
  const {
    router: { history }
  } = context;

  return (
    <React.Fragment>
      <div className={pageHeaderClasses('container-1')}>
        <div className={pageHeaderClasses('header-item', 'logo')}>
          <APLogo />
        </div>
        <PageHeaderNavComponent />
        <div className={pageHeaderClasses('right-panel')}>
          <UserSummaryContainer customClass="page-header-user-summary" />
          <div
            className={pageHeaderClasses('header-item', [
              'last',
              'hoverable',
              history.location.pathname.includes('settings') ? 'active' : ''
            ])}
            onClick={() => history.push(path(mainNavOptions.settingsPath))}>
            <APIcon
              name="user-settings"
              key="settings"
              customClass={pageHeaderClasses('icon')}
            />
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

PageHeaderComponent.contextTypes = {
  router: PropTypes.shape({
    history: PropTypes.object.isRequired
  }).isRequired,
  t: PropTypes.func.isRequired
};

export default PageHeaderComponent;



