import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';
import { mainNavOptions } from '../../routeTemplates';
import path from '../../helpers/path';
import APIcon from '../common/APIcon/APIcon';

const PageHeaderNavMenuComponent = (props, context) => {
  const { customClass, handleNavItemClick } = props;

  const isActiveNavItem = (match, location, item) => {
    const pathChunks = item.path.split('/');
    if (item.path.includes('retail')) {
      if (location.pathname.includes(pathChunks[2])) return true;
    } else if (location.pathname.includes(pathChunks[1])) return true;
    return false;
  };

  return mainNavOptions.mainNavItems.map(item => (
    <NavLink
      key={item.path}
      to={path(item.path)}
      onClick={handleNavItemClick}
      className={customClass('item', ['hoverable'])}
      isActive={(match, location) => isActiveNavItem(match, location, item)}
      activeClassName={customClass('item', ['selected'])}>
      <APIcon name={item.icon} customClass={customClass('icon')} />
      <span className={customClass('label')}>{context.t(item.text)}</span>
    </NavLink>
  ));
};

PageHeaderNavMenuComponent.propTypes = {
  customClass: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
  items: PropTypes.array
};

PageHeaderNavMenuComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default PageHeaderNavMenuComponent;



